import {select} from 'd3-selection';

export default class Util {

    /**
     *  svg  dom
     *
     * @param  {HTMLElement} dom      
     * @param  {number}      width    
     * @param  {number}      height   
     * @return {Object}               d3SVG
     */
    static createSVG(dom, width, height) {
        return select(dom)
            .append('svg')
            .attr('width', width)
            .attr('height', height);
    }

    /**
     *  node 
     *
     * @param  {Object} node    D3Selection
     * @param  {number} x       x
     * @param  {number} y       y
     * @param  {string} content 
     * @return {Object}         D3Selection
     */
    static text(node, x, y, content) {
        return node.append('text').attr('x', x).attr('y', y).text(content);
    }

    /**
     *  node 
     *
     * @param  {Object} node D3Selection
     * @param  {number} cx   x
     * @param  {number} cy   y
     * @param  {number} r    
     * @return {Object}      D3Selection
     */
    static circle(node, cx, cy, r) {
        return node.append('circle').attr('cx', cx).attr('cy', cy).attr('r', r);
    }

    /**
     *  multiplier1 multiplier
     *
     * @param  {number} value      
     * @param  {number} min        
     * @param  {number} max        
     * @param  {number} multiplier 
     * @return {number}             * multiplier
     */
    static normalize(value, min, max, multiplier = 1) {
        return (value - min) * multiplier / (max - min);
    }

    /**
     * 
     *
     * @param  {Date} date 
     * @return {Array.<Date>}     
     */
    static getWeek(date) {
        let time = date.getTime();
        let day = date.getDay();
        let timeOfDay = 24 * 60 * 60 * 1000;

        if (day === 0) {
            return [
                new Date(time - 6 * timeOfDay),
                new Date(time)
            ];
        }

        return [
            new Date(time - (day - 1) * timeOfDay),
            new Date(time + (7 - day) * timeOfDay)
        ];
    }

    /**
     * 
     *
     * @param  {Date} startDate 
     * @param  {Date} endDate   
     * @return {Array.<Object>} 
     */
    static getRangeWeeks(startDate, endDate) {
        let startTime = startDate.getTime();
        let endTime = endDate.getTime();
        let timeOfDay = 24 * 60 * 60 * 1000;
        let weeks = [];

        while (endTime > startTime) {
            let [endWeekStart, endWeekEnd] = this.getWeek(endDate);
            weeks.push({
                start: endWeekStart,
                end: endWeekEnd
            });

            endDate = new Date(endTime - 7 * timeOfDay);
            endTime = endDate.getTime();
        }

        // 
        let [startWeekStart, startWeekEnd] = this.getWeek(startDate);
        weeks.push({
            start: startWeekStart,
            end: startWeekEnd
        });

        return weeks;
    }

    /**
     * 
     *
     * @param  {Date} date       
     * @param  {string} splitter '.' 2016.09.17
     * @return {string}          
     */
    static formatDate(date, splitter = '.') {
        return '' + date.getFullYear()
            + splitter
            + (date.getMonth() + 1)
            + splitter
            + (date.getDate());
    }

    /**
     * d3selection
     *
     * @param  {Selection}    selection d3
     * @param  {...Array}     args      
     * @return {Selection}              d3
     */
    static attr(selection, ...args) {

        let firstArg = args[0];
        let secondArg = args[1];

        if (typeof firstArg === 'object') {
            for (let key in firstArg) {
                if (firstArg.hasOwnProperty(key)) {
                    selection.attr(key, firstArg[key]);
                }
            }
        }
        else if (typeof firstArg === 'string') {
            selection.attr(firstArg, secondArg);
        }

        return selection;
    }
}



