import '../static/css/demand.less';
import dragImagePng from '../static/img/zoomhander.png';

import Util from '../common/util';
import ee from '../common/event';

import _extend from 'lodash-es/extend';
import _map from 'lodash-es/map';
import _each from 'lodash-es/each';
import _shuffle from 'lodash-es/shuffle';
import _uniq from 'lodash-es/uniq';
import _isUndefined from 'lodash-es/isUndefined';
import _random from 'lodash-es/random';
import _find from 'lodash-es/find';
import _filter from 'lodash-es/filter';
import _minBy from 'lodash-es/minBy';
import _findIndex from 'lodash-es/findIndex';

import {range as d3Range, extent as d3Extent} from 'd3-array';
import {select as d3Select, event as d3Event} from 'd3-selection';
import {drag as d3Drag} from 'd3-drag';
import 'd3-transition';

import Chart from './chart';

/**
 * 
 *
 * @type {Object}
 */
const defaultOptions = {

    // ,  '#main'
    el: 'body',

    // 
    width: 990,
    height: 530,

    // 
    R_MIN: 10,
    R_MAX: 30,

    // 
    random: false,

    /**
     * 
     *
     * {
     *     // 0-
     *     radius: 100,
     *
     *     // 1- radius + radiusOffset
     *     //   10030[70~130]
     *     maxRadiusOffset: 30,
     *
     *     // 2-1/4 [-angle, angle]  [-angle + 180, angle + 180]
     *     angle: 10,
     *
     *     // 3-4
     *     minWordLength: 4,
     *
     *     // 4-8
     *     maxWordLength: 8
     * }
     *
     * @type {Array}
     */
    group: [
        [100, 30, 80, 4, 8],
        [200, 30, 50, 4, 8],
        [300, 30, 30, 4, 10],
        [400, 30, 23, 4, 12]
    ],

    /**
     * 
     *
     * {
     *     "name": "", // 
     *     "query": 4975, // 
     *     "r": 1055,     // 
     *     "up": 1        // up1-1
     * }
     *
     * @type {Array}
     */
    data: [],

    // (0, 0)
    center: '',

    // 
    upColor: '#fa7c62',

    // 
    downColor: '#20b78e',

    // 
    strokeColor: '#fff',

    // 
    showControl: true,

    // 
    auto: true,

    // 
    startDate: new Date(2015, 9, 4),

    // 
    endDate: new Date(2016, 8, 25)
};

/**
 * 
 */
class Timeline {

    constructor(options) {
        this.options = options;

        // 
        this.index = 0;

        // 
        this.count = this.options.weekData.length;

        // 
        this.init();
    }

    /**
     * 
     */
    init() {
        this.svg = this.options.svg;

        // 
        this.g = this.svg.append('g').style('opacity', 0);

        // 
        this.rects = this.createTimelinePoints();

        // 
        this.labels = this.createTimelineLabels();

        // 
        this.indicator = this.createIndicator();

        // 
        this.centerTimeline();

        // 
        this.tooltip = this.createTooltip();

        if (this.options.showControl) {
            // 
            this.startButton = this.createStartButton();

            // 
            this.prevButton = this.createPrevButton();

            // 
            this.nextButton = this.createNextButton();
        }

        // 
        // this.start(this.options.auto);
    }

    /**
     * /
     *
     * @return {Selection} d3(div)
     */
    createStartButton() {
        let auto = this.options.auto;
        let startButton = this.options.wrapper.append('div')
            .attr('class', auto ? 'demand-pause-button' : 'demand-start-button')
            .style('left', this.g.left + 'px')
            .style('top', this.g.top + 'px')
            .attr('title', '/');

        startButton.on('click', this.onStart.bind(this));
        return startButton;
    }

    /**
     * /
     */
    onStart() {

        // 
        clearTimeout(this.timeout);

        // 
        let isRunning = this.startButton.classed('demand-pause-button');

        // 
        if (isRunning) {
            this.stopAutorun();
        }
        else {
            this.startButton.classed('demand-start-button', false);
            this.startButton.classed('demand-pause-button', true);

            this.start(true);
        }
    }

    /**
     * 
     */
    stopAutorun() {

        // 
        clearTimeout(this.timeout);
        delete this.timeout;

        // 
        if (this.options.showControl) {
            this.startButton.classed('demand-pause-button', false);
            this.startButton.classed('demand-start-button', true);
        }

        // 
        this.tooltip.style('opacity', 0);
    }

    /**
     * 
     *
     * @return {Selection} d3(div)
     */
    createPrevButton() {
        let prevButton = this.options.wrapper.append('div')
            .attr('class', 'demand-prev-button')
            .style('left', (this.g.left - 25) + 'px')
            .style('top', this.g.top + 'px')
            .attr('title', '');

        prevButton.on('click', this.onPrev.bind(this));
        return prevButton;
    }

    /**
     * 
     */
    onPrev() {
        this.stopAutorun();
        this.prev();
    }

    /**
     * 
     *
     * @return {Selection} d3(div)
     */
    createNextButton() {
        let nextButton = this.options.wrapper.append('div')
            .attr('class', 'demand-next-button')
            .style('left', (this.g.left + 25) + 'px')
            .style('top', this.g.top + 'px')
            .attr('title', '');

        nextButton.on('click', this.onNext.bind(this));
        return nextButton;
    }

    /**
     * 
     */
    onNext() {
        this.stopAutorun();
        this.next();
    }

    /**
     * 
     *
     * @return {Selection} d3(div)
     */
    createTooltip() {
        let top = this.g.top - 50;
        let tooltip = this.options.wrapper.append('div')
            .attr('class', 'demand-tool-tip')
            .style('top', top + 'px');

        return tooltip;
    }

    /**
     * 
     *
     * @return {Selection} d3image
     */
    createIndicator() {

        let indicator = this.g.append('image');
        Util.attr(indicator, {
            'class': 'demand-indicator',
            'preserveAspectRatio': 'none',

            //  rollup  src 
            //  webpack 
            'xlink:href': (dragImagePng.src || dragImagePng),
            'opacity': 0,
            'y': -15
        });

        // 
        let dragBehavior = d3Drag()
            .on('start', this.onDragStart.bind(this))
            .on('drag', this.onDrag.bind(this))
            .on('end', this.onDragEnd.bind(this));

        indicator.call(dragBehavior);
        return indicator;
    }

    /**
     * 
     */
    onDragStart() {

        // 
        clearTimeout(this.timeout);
    }

    /**
     * 
     */
    onDrag() {
        let x = d3Event.x;
        let sourceEvent = d3Event.sourceEvent;
        let leftEnd = this.g.left;
        let rightEnd = this.g.right;
        let indicator = this.indicator;

        // 
        x > rightEnd && (x = rightEnd - 22);
        x < leftEnd && (x = leftEnd - 8);
        indicator.attr('x', x);

        // 
        let weekData = this.options.weekData;
        let nearestPoint = _minBy(weekData, item => Math.abs(x - item.x));

        // 
        this.tooltip
            .text(nearestPoint.start + ' - ' + nearestPoint.end)
            .style('left', (sourceEvent.clientX - 75) + 'px')
            .style('opacity', 1);
    }

    /**
     * 
     */
    onDragEnd() {
        let x = d3Event.x;

        let weekData = this.options.weekData;
        let nearestPoint = _minBy(weekData, item => Math.abs(x - item.x));
        let index = _findIndex(weekData, item => item.x === nearestPoint.x);

        // 
        this.indicator.attr('x', nearestPoint.x);
        this.tooltip.style('opacity', 0);

        // 
        this.index = index;

        // 
        this.stopAutorun();
        this.go(index);
    }

    /**
     * 
     */
    centerTimeline() {
        let {svgHeight, svgWidth, showControl} = this.options;
        let {width, height, left, right} = this.g.node().getBoundingClientRect();
        let marginBottom = 55;
        let leftOffset = showControl ? 50 : 0;
        let x = (svgWidth - width) / 2 + leftOffset;
        let y = svgHeight - marginBottom;

        // 
        this.g.transition().duration(200)
            .attr('transform', `translate(${x}, ${y})`)
            .style('opacity', 1);

        // 
        this.g.left = left + leftOffset;
        this.g.right = right;
        this.g.top = y;
        this.g.height = height;
        this.g.width = width;
    }

    /**
     * 
     *
     * @return {Selection} d3(rect)
     */
    createTimelinePoints() {
        let weekData = this.options.weekData;

        // 
        let rectWidth = 14;
        let rectHeight = 10;

        // 
        let gap = 1.5;
        let points = this.g.selectAll('rect.date-bar-item')
            .data(weekData)
            .enter()
            .append('rect')
            .attr('class', 'date-bar-item')
            .attr('width', rectWidth)
            .attr('height', rectHeight)
            .attr('x', (d, i) => {
                let x = (rectWidth + gap) * i;

                //  x 
                d.x = x;
                return x;
            })
            .attr('y', d => {
                let y = 0;

                //  y 
                d.y = 0;
                return y;
            });

        // 
        points.on('click', (d, i) => {
            this.stopAutorun();
            this.go(i);
        });

        return points;
    }

    /**
     * 
     *
     * @return {Selection} d3(text)
     */
    createTimelineLabels() {
        let labelTexts = this.formatTimelineLabels();

        // 
        let leftOffset = 48;
        let labels = this.g.selectAll('text.demand-month-label')
            .data(labelTexts)
            .enter()
            .append('text')
            .attr('class', 'demand-month-label')
            .attr('x', (d, i) => leftOffset + 4 * 17 * i)
            .attr('y', 40)
            .text(d => d);

        return labels;
    }

    /**
     *  "3"1 "20161"
     *
     * @return {Array.<string>} 
     */
    formatTimelineLabels() {
        let weekData = this.options.weekData;
        let texts = [];

        let oldValue = '';
        _each(weekData, item => {
            let [year, month] = item.end.split('.');
            let newValue = +month === 1 ? year + '' + month + '' : month + '';

            if (newValue !== oldValue) {
                texts.push(newValue);
            }

            oldValue = newValue;
        });

        return texts;
    }

    /**
     * 
     *
     * @param {boolean} auto 
     */
    start(auto) {
        let me = this;
        let i = me.index || 0;

        // 
        if (auto) {
            me.timeout = setTimeout(() => {
                (i === me.count -1) && (i = 0);
                me.go(++i);
                me.start(auto);
            }, 2000);
        }
        else {
            me.go(i);
        }
    }

    /**
     *  index 
     *
     * @param {number} index 
     */
    go(index) {
        let weekData = this.options.weekData;
        let point = weekData[index];

        this.indicator.attr('opacity', 1);
        this.indicator.transition().duration(500).attr('x', point.x);
        this.index = index;

        this.tooltip.transition().duration(500)
            .text(point.start + ' - ' + point.end)
            .style('left', (this.g.left + point.x + 15) + 'px')
            .style('opacity', 1);

        // 
        ee.emit('loadData', point);
    }

    /**
     * 
     */
    next() {
        this.index === this.count - 1
            ? this.go(0)
            : this.go(this.index + 1);
    }

    /**
     * 
     */
    prev() {
        this.index === 0
            ? this.go(this.count - 1)
            : this.go(this.index - 1);
    }

    /**
     * 
     */
    dispose() {
        if (this.tooltip) {
            let node = this.tooltip.node();
            node.parentNode && node.parentNode.removeChild(node);

            delete this.tooltip;
        }

        clearTimeout(this.timeout);
        delete this.timeout;
    }
}

/**
 * 
 *
 * @class 
 * @extends Chart
 */
export default class Demand extends Chart {

    /**
     * 
     *
     * @return {string} 
     */
    static type() {
        return 'Demand';
    }

    /**
     * 
     *
     * @param  {Object} options 
     */
    constructor(options) {
        super();

        this.options = _extend({}, defaultOptions, options);
        this.init();
    }

    /**
     * 
     */
    init() {

        // 
        this.wrapper = this.createBackground();

        // SVG
        [this.svg, this.svgGroup] = this.createSVG();

        // 
        this.renderCenterWord();

        // 
        this.timeline = this.createTimeline();

        // 
        this.words = this.participle();

        // 
        this.elements = this.createPoints();

        // 
        this.start();
    }

    /**
     * 
     *
     * @param  {string}   eventName 
     * @param  {Function} fn        
     */
    on(eventName, fn) {
        let blank = function () {};
        ee.on(eventName, fn || blank);
    }

    /**
     * 
     *
     * @return {Timeline} 
     */
    createTimeline() {

        let {startDate, endDate, width, height, showControl, auto} = this.options;
        let weeks = Util.getRangeWeeks(startDate, endDate).reverse();
        let weekData = _map(weeks, (item, i) => {
            return {
                index: i,
                start: Util.formatDate(item.start),
                end: Util.formatDate(item.end)
            };
        });

        return new Timeline(
            {
                wrapper: this.wrapper,
                svg: this.svg,
                svgWidth: width,
                svgHeight: height,
                startDate,
                endDate,
                weekData,
                auto,
                showControl
            }
        );
    }

    /**
     * 
     */
    start() {

        // 
        this.timeline.start(this.options.auto);
    }

    /**
     * 
     * @return {Array} 
     */
    createPoints() {
        let points = this.getPoints();
        let elements = [];

        _each(points, point => {
            let g = this.svgGroup.append('g');

            let circle = Util.circle(g, point.x, point.y, point.cr)
                .attr('fill', point.fillColor)
                .attr('stroke', point.strokeColor);

            let text = Util.text(g, point.x, point.y + point.cr + 15, point.name)
                .attr('text-anchor', 'middle')
                .attr('font-size', 12);

            elements.push({
                x: point.x,
                y: point.y,
                cr: point.cr,
                fillColor: point.fillColor,
                root: g,
                circle,
                text,
                name: point.name,
                groupIndex: point.groupIndex
            });
        });

        return elements;
    }

    /**
     * 
     *
     * @return {Array} 
     */
    getPoints() {
        let {R_MIN, R_MAX, group, downColor, upColor, strokeColor, random} = this.options;
        let points = [];

        // 
        let [min, max] = d3Extent(this.options.data, item => item.query);

        _each(this.words, (wordgroup, i) => {

            // 
            let groupItem = group[i];

            // 
            let groupLen = wordgroup.length;

            // 1/4
            let angle = groupItem[2];

            // 
            let angleSpan = 2 * angle / groupLen;

            // 
            if (angleSpan < 10) {
                angleSpan = 10;
            }

            // 
            let angles = d3Range(-angle - 5, angle, angleSpan)
                .concat(d3Range(180 - angle, 180 + angle + 5, angleSpan));

            // (0-360)
            angles = angles.map(item => (item + 360) % 360);

            // 
            angles = _uniq(angles);

            // 
            if (random) {
                angles = _shuffle(angles);
            }
            else {
                angles = this.divideAngles(angles);
            }

            // 
            _each(wordgroup, word => {

                //  pop 
                let xangle = angles.pop();
                if (!_isUndefined(xangle)) {

                    // 
                    let r = groupItem[0] + _random(0, groupItem[1]);

                    //  xy   r
                    let x = r * Math.cos(xangle * Math.PI / 180);
                    let y = r * Math.sin(xangle * Math.PI / 180) - 20;
                    let cr = R_MIN + Util.normalize(word.query, min, max, R_MAX - R_MIN);

                    let fillColor = downColor;
                    if (word.up === 1) {
                        fillColor = upColor;
                    }
                    let name = word.name;

                    // 
                    let groupIndex = i;

                    points.push({x, y, cr, fillColor, strokeColor, name, groupIndex});
                }
            });
        });

        return points;
    }

    divideAngles(angles) {
        let result = [];
        let ones = angles.filter(item => (item > 0 && item < 90));
        let twos = angles.filter(item => (item > 90 && item < 180));
        let threes = angles.filter(item => (item > 180 && item < 270));
        let fours = angles.filter(item => (item > 270 && item < 360));
        let len = Math.max(ones.length, twos.length, threes.length, fours.length);
        for (let i = 0; i < len; i++) {
            ones.length && result.push(ones.shift());
            twos.length && result.push(twos.shift());
            threes.length && result.push(threes.shift());
            fours.length && result.push(fours.shift());
        }
        console.log(result);
        return result;
    }

    /**
     * 
     *
     * @param {Array} data 
     */
    setData(data) {
        this.options.data = data;

        // 
        this.words = this.participle();

        // 
        this.refreshLoad();
    }

    refreshLoad() {

        // 
        let points = this.getPoints();
        let {width, height} = this.options;

        // 
        let oldWords = _map(this.elements, item => item.name);
        let oldWordsMap = {};
        _each(oldWords, item => {
            oldWordsMap[item] = 1;
        });

        let g = this.elements[0].root;

        // 
        let myMap = {
            same: [],
            diff: []
        };

        // 
        _each(this.words, (wordGroup, i) => {

            // 
            _each(wordGroup, word => {

                // 
                if (oldWordsMap[word.name]) {

                    // 
                    let from = _find(this.elements, item => item.name === word.name);

                    // 
                    let groupPoints = _filter(points, item => item.groupIndex === i && !item.selected);

                    // 
                    let targetGroupPoint = _find(points, item => item.name === word.name);

                    // 
                    let minPoint = _minBy(groupPoints, p => Math.sqrt(
                        (p.x - from.x) * (p.x - from.x) + (p.y - from.y) * (p.y - from.y))
                    );

                    // 
                    minPoint.selected = true;

                    // 
                    let to = {
                        x: minPoint.x,
                        y: minPoint.y,
                        cr: targetGroupPoint.cr,
                        fillColor: targetGroupPoint.fillColor
                    };

                    //  from  to 
                    myMap.same.push({
                        from,
                        to
                    });

                    // 
                    delete oldWordsMap[word.name];
                }
                else {
                    // 

                    // 
                    let groupPoints = _filter(points, item => item.groupIndex === i && !item.selected);

                    // 
                    let targetGroupPoint = _find(points, item => item.name === word.name);

                    // 
                    let to = groupPoints[groupPoints.length - 1];

                    let tx = 0;
                    let ty = 0;
                    if (to.x > 0) {
                        tx = width / 2 + 20;
                        ty = height / 2 + 20;
                    }
                    else {
                        tx = - width / 2 - 20;
                        ty = - height / 2 - 20;
                    }

                    let circle = Util.circle(g, tx, ty, targetGroupPoint.cr)
                        .attr('fill', targetGroupPoint.fillColor)
                        .attr('stroke', targetGroupPoint.strokeColor);

                    let text = Util.text(g, tx, ty + targetGroupPoint.cr + 15, word.name)
                        .attr('text-anchor', 'middle')
                        .attr('font-size', 12);

                    to.selected = true;

                    this.elements.push({
                        x: to.x,
                        y: to.y,
                        cr: targetGroupPoint.cr,
                        fillColor: targetGroupPoint.fillColor,
                        name: word.name,
                        groupIndex: i,
                        text,
                        circle
                    });

                    myMap.diff.push({
                        from: {
                            text,
                            circle
                        },
                        to: {
                            x: to.x,
                            y: to.y,
                            cr: targetGroupPoint.cr,
                            fillColor: targetGroupPoint.fillColor
                        }
                    });

                }
            });
        });

        // 
        _each(oldWordsMap, (v, word) => {
            let el = this.elements.find(item => item.name === word);
            let index = this.elements.findIndex(item => item.name === word);

            this.deleteNode(el.circle);
            this.deleteNode(el.text);

            this.elements.splice(index, 1);
        });

        _each(myMap.same.concat(myMap.diff), item => {
            let from = item.from;
            let to = item.to;
            from.circle.transition().duration(750).attr('cx', to.x).attr('cy', to.y);
            from.text.transition().duration(750)
                .attr('x', to.x)
                .attr('y', to.y + to.cr + 15);
        });
    }

    deleteNode(selection) {
        let node = selection.node();
        node.parentNode && node.parentNode.removeChild(node);
    }

    /**
     * 
     *
     * @return {Selection} 
     */
    createBackground() {
        return d3Select(this.options.el).append('div').attr('class', 'chart-demand');
    }

    /**
     * SVGg
     *
     * @return {Array.<Selection>} SVGg
     */
    createSVG() {
        let {width, height} = this.options;
        let svg = this.wrapper.append('svg').attr('width', width).attr('height', height).attr('class', 'd3-svg');

        // 
        let xOffset = -1;
        let yOffset = -9;
        let x0 = width / 2 + xOffset;
        let y0 = height / 2 + yOffset;

        // (x0, y0)
        let root = svg.append('g').attr('transform', `translate(${x0}, ${y0})`);

        //  svg  root 
        return [svg, root];
    }

    /**
     * 
     */
    renderCenterWord() {
        let centerWord = this.options.center;
        Util.text(this.svgGroup, 0, 7, centerWord).attr('class', 'demand-center');
    }

    /**
     *  group  4 
     *
     * @return {Array} 
     */
    participle() {
        let {data: words, group} = this.options;

        // 
        let [minRalation, maxRelation] = d3Extent(words, item => item.r);

        // 
        _each(words, item => {
            item.r = parseInt(Util.normalize(item.r, minRalation, maxRelation, 100), 10);
        });

        //  group  (r) 
        let groupNum = group.length;
        let groupArr = [];
        let i = groupNum;
        while (i--) {
            groupArr.push([]);
        }

        // 
        let gval = 100 / groupNum;
        _each(words, item => {
            let n = Math.round(item.r / gval);
            (n >= groupNum) && (n -= 1);

            if (groupArr[groupNum - 1 - n] !== undefined) {
                groupArr[groupNum - 1 - n].push(item);
            }
        });

        _each(group, (item, i) => {

            // 
            let gmin = item[3];

            // 
            let gmax = item[4];

            let garr = groupArr[i];
            let glen = garr.length;

            //  
            if (glen > gmax) {
                groupArr[i] = garr.slice(0, gmax);
                if (groupArr[i + 1] !== undefined) {
                    groupArr[i + 1] = garr.slice(gmax).concat(groupArr[i + 1]);
                }
            }
            //  
            else if (glen < gmin) {
                for (let gi = i + 1; gi < groupNum; gi++) {
                    if (groupArr[gi] !== undefined && groupArr[gi].length > 0) {
                        groupArr[i] = groupArr[i].concat(groupArr[gi]);

                        if (groupArr[i].length >= gmin) {
                            //  
                            groupArr[gi] = groupArr[i].slice(gmin);
                            groupArr[i] = groupArr[i].slice(0, gmin);
                            break;
                        }
                        else {
                            groupArr[gi] = [];
                        }
                    }
                }
            }
        });

        return groupArr;
    }

    /**
     * 
     */
    dispose() {
        this.timeline && this.timeline.dispose();
    }
}



